import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AuthGuard } from "./shared";
import { CommonGuard } from "./shared/guards/common.guard";

const routes: Routes = [
  // { path: '', loadChildren: () => import('./layout/layout.module').then(m => m.LayoutModule), canActivate: [AuthGuard] },
  {
    path: "",
    loadChildren: () =>
      import("./layout/layout.module").then(m => m.LayoutModule),
    canActivate: [CommonGuard]
    // canLoad: [CommonGuard]
  },
  {
    path: "login",
    loadChildren: () => import("./login/login.module").then(m => m.LoginModule),
    canActivate: [AuthGuard],
  },
  {
    path: "signup",
    loadChildren: () =>
      import("./signup/signup.module").then(m => m.SignupModule),
    canActivate: [AuthGuard],
  },
  {
    path: "error",
    loadChildren: () =>
      import("./server-error/server-error.module").then(
        m => m.ServerErrorModule
      )
  },
  {
    path: "access-denied",
    loadChildren: () =>
      import("./access-denied/access-denied.module").then(
        m => m.AccessDeniedModule
      )
  },
  {
    path: "not-found",
    loadChildren: () =>
      import("./not-found/not-found.module").then(m => m.NotFoundModule)
  },
  {
    path: 'users-list',
    loadChildren: () => import('./layout/users/users.module').then(m => m.UsersModule),
    canActivate: [AuthGuard],
  },
  {
    path: 'pos',
    loadChildren: () => import('./layout/pos/pos.module').then(m => m.PosModule)
  },
  {
    path: 'hotels',
    loadChildren: () => import('./layout/search/hotels/hotels.module').then(m => m.HotelsModule)
  },
  { 
    path: 'roles',
    loadChildren: () => import('./layout/setup-configuration/roles/roles.module').then(m => m.RolesModule)
  }

  // { path: '**', redirectTo: 'not-found' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload' })],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule {}
