import { CoreModule } from './shared/loader';
import { AuthGuard } from "./shared";
import { NgModule } from "@angular/core";
import { AppRoutingModule } from "./app-routing.module";
import { AppComponent } from "./app.component";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { ReactiveFormsModule, FormsModule } from "@angular/forms";
import { ServiceWorkerModule } from "@angular/service-worker";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { FlexLayoutModule } from "@angular/flex-layout";
import { CKEditorModule } from "ckeditor4-angular";
import { CommonModule } from "@angular/common";
import { DragDropModule } from "@angular/cdk/drag-drop";
import { SecurityInterceptor } from "./shared/guards/security.interceptor";
import { TokenInterceptor } from "./shared/guards/token.interceptor";
import { ToastrModule } from "ngx-toastr";
import { ApplicationAccess } from "./access.module";
import { SearchService } from "./shared/services/search.service";
//import { MatChipsModule } from '@angular/material/chips';
import { Globals } from './shared/utils/globals';
//import { SocketIoModule, SocketIoConfig } from 'ngx-socket-io';
import { CookieService } from 'ngx-cookie-service';

import { environment } from '../environments/environment';

// import { NgxUiLoaderModule, NgxUiLoaderHttpModule } from  'ngx-ui-loader';

import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreRouterConnectingModule, RouterState } from '@ngrx/router-store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';

import { ROOT_REDUCERS, metaReducers } from './reducers';

import { UserEffects, RouterEffects } from './core/effects';


// const config: SocketIoConfig = {
//   url: environment.socketURL,
//   options: {}
// }

@NgModule({
  declarations: [AppComponent],
  imports: [
    FormsModule,
    CommonModule,
    ReactiveFormsModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FlexLayoutModule,
    CKEditorModule,
    DragDropModule,
    ToastrModule.forRoot({
      preventDuplicates: true,
      closeButton: true,
      positionClass: 'toast-bottom-right'
    }),
    ServiceWorkerModule.register("./ngsw-worker.js", {
      enabled: environment.production
    }),
    ServiceWorkerModule.register('./ngsw-worker.js', { enabled: environment.production }),
    ApplicationAccess,
    CoreModule,
    //MatChipsModule,
    ToastrModule.forRoot({
    }),
    ServiceWorkerModule.register('./ngsw-worker.js', { enabled: environment.production }),

    StoreModule.forRoot(ROOT_REDUCERS, {
      metaReducers,
      runtimeChecks: {
        // strictStateImmutability: true,
        // strictActionImmutability: true,
        // strictStateSerializability: true,
        // strictActionSerializability: true
      }
    }),

    /**
     * @ngrx/router-store keeps router state up-to-date in the store.
     */
    StoreRouterConnectingModule.forRoot(),

    StoreDevtoolsModule.instrument({
      name: 'NgRx TPConnects - Aggregator Platform',
      logOnly: environment.production
    }),

    /**
     * EffectsModule.forRoot() is imported once in the root module and
     * sets up the effects class to be initialized immediately when the
     * application starts.
     *
     * See: https://ngrx.io/guide/effects#registering-root-effects
     */
    EffectsModule.forRoot([UserEffects, RouterEffects]),

    //SocketIoModule.forRoot(config),
    // NgxUiLoaderModule,
    // NgxUiLoaderHttpModule,
 
  ],
  providers: [
    Globals,
    AuthGuard,
    CookieService,
    SearchService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: SecurityInterceptor,
      multi: true
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
